import React, { Component } from 'react';
import { Button } from '@material-ui/core';
import './login.css';
import LoginImage from './LoginImage';
import { BackdropLoader } from '../services/loader';
import FormComponent from '../Form/FormComponent';
import { resetPwdConfig } from './ResetPasswordConfig';
import { resetPassword,verifyForgetPasswordReq } from '../services/authService';
import swal from 'sweetalert';

class ResetPassword extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loader: false,
      authToken: null
    };
  }
  componentDidMount() {
    console.log('this.props ',this.props);
    if(this.props){
      const encode = this.props.match && this.props.match.params ? this.props.match.params.encode : null;
      if(encode) {
        this.setState({loader: true})
        verifyForgetPasswordReq(encode).then(response => {
          let authToken = null;
          if(response && response.data){
            authToken = response.data.authToken
          }
          this.setState({loader: false,authToken})
        }).catch(err => {
          // this.setState({loader: false})
          console.log('Error : ',err);
        })
      }
    }
  }
  preSubmitChanges = (e) => {
    console.log('RRRRR : ',e);
    this.setState({loader: true})
    // ,this.state.authToken
    resetPassword(e.password,this.state.authToken).then(response => {
      if(response) {
        if(response && response.status === 200){
          swal({
            text: "User Password Updated.",
            icon: "success",
          }).then(resp => {
            this.props.history.push('/login')
          });
        }
      }
      this.setState({loader: false})
    }).catch(err => {
      console.log(err);
      if(err.response.status === 401) {
        swal({
          text: "Invalid bearer token or not presented.",
          icon: "error",
        })
      }
      this.setState({loader: false})
    })
    //Reset button action
    // return false;
  };

  onSignIn = () => {
    this.props.history.push('/login');
  };

  render() {
    return (
      <div className='signMain'>
        <BackdropLoader open={this.state.loader} />
        <LoginImage />
        <div className='signForm'>
          <div className='logoPart'>
            <span className='logo'>
              <svg
                xmlns='http://www.w3.org/2000/svg'
                xlink='http://www.w3.org/1999/xlink'
                width='78'
                height='87'
                viewBox='0 0 78 87'
              >
                <defs>
                  <clipPath id='clip-path'>
                    <rect
                      id='Rectangle_371'
                      data-name='Rectangle 371'
                      width='78'
                      height='87'
                      transform='translate(-2269 -5850)'
                      fill='#fff'
                      opacity='0.2'
                    />
                  </clipPath>
                </defs>
                <g
                  id='logo'
                  transform='translate(2269 5850)'
                  clipPath='url(#clip-path)'
                >
                  <g
                    id='Group_1069'
                    data-name='Group 1069'
                    transform='translate(-3989.496 -5953.821)'
                  >
                    <path
                      id='Oval-3'
                      d='M37.529,6.955a38.144,38.144,0,0,0,6.084,75.8'
                      transform='translate(1701.713 146.92) rotate(-45)'
                      fill='none'
                      stroke='#fff'
                      strokeLinecap='square'
                      strokeWidth='9'
                      fillRule='evenodd'
                    />
                    <path
                      id='Oval-3-2'
                      data-name='Oval-3'
                      d='M81.755,44.614A38.187,38.187,0,0,0,70.912,17.976'
                      transform='translate(1702.054 146.92) rotate(-45)'
                      fill='none'
                      stroke='#fff'
                      strokeLinecap='square'
                      strokeWidth='9'
                      fillRule='evenodd'
                    />
                    <path
                      id='Path_435'
                      data-name='Path 435'
                      d='M.1,1.979l9.979-3.887-.031,31.94H31.026l-.031,9.953H10V44s-.037,5.375,7.8,7.348a15.406,15.406,0,0,0,11.313-1.856l6.127,6.252a26.852,26.852,0,0,1-22.372,3.5C-.248,53.378,0,44,0,44Z'
                      transform='translate(1744 108)'
                      fill='#fff'
                    />
                  </g>
                </g>
              </svg>
            </span>
            <span className='logoText'>ConsenTrade</span>
          </div>
          <h2>Reset Password</h2>
          <div className='resetPasswordform'>
            <FormComponent
              formConfig={resetPwdConfig}
              preSubmitChanges={this.preSubmitChanges}
              continueButton='RESET PASSWORD'
              modalCloseCallback={() => {}}
            />
            <Button
              variant='contained'
              className='loginOptional'
              onClick={() => {
                this.onSignIn();
              }}
            >
              LOGIN
            </Button>
          </div>
        </div>
      </div>
    );
  }
}
export default ResetPassword;
