import React, { Component } from 'react';
import { AppBar, Tabs, Tab, Typography, Box } from '@material-ui/core';
import PropTypes from 'prop-types';
import { DotLoader } from 'react-spinners';
import PaymentCertificatePurchase from './PaymentCertificatePurchase';
import PaymentCertificateSales from './PaymentCertificateSales';
import {getLocalDocumentsList} from "../../services/authService";
import {filterOnDocType} from "../Shared/Documents/DocumentsDataFormat"
import {getTimeFromUnixTimestamp, unixTimestampToDate} from "../Shared/dateTimeFormat"
import {revokeDocument} from "../../services/createDocumentService"
import {getDocumentListFun} from '../Shared/Documents/getDocumentsList'
import { BackdropLoader } from '../../services/loader';
import { Dialog, IconButton } from '@material-ui/core';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import { withStyles } from '@material-ui/core/styles';
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import signDocService from '../../services/signDocument';
import PaymentCertTemplate from '../Shared/Documents/PaymentCertTemplate';
import RefreshIcon from '@material-ui/icons/Refresh';
import Tooltip from '@material-ui/core/Tooltip';



import './PaymentCertificate.css';
import { formatAmount, numberWithCommas } from '../Shared/amountFormat';

const styles = (theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(2),
  },
  closeButton: {
    position: 'absolute',
    left: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
});

const DialogTitle = withStyles(styles)((props) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      {onClose ? (
        <IconButton
          aria-label='close'
          className={classes.closeButton}
          onClick={onClose}
        >
          <ArrowBackIosIcon />
        </IconButton>
      ) : null}
      <Typography variant='h6' align={children ? 'center' : 'left'}>
        {children}
      </Typography>
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Typography
      component='div'
      role='tabpanel'
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      <Box>{children}</Box>
    </Typography>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

class PaymentCertificateTab extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: 0,
      issuedBy: undefined,
      issuedTo: undefined,
      loader: false,
      loader1: false,
      openModalQuotation: false,
      document: {
        doc: null,
        issuerDetails: null,
        recipientDetails: null,
      },
    };
  }

  async componentDidMount() {
    this.getPaymentData()
    // this.setState({
    //   sales: SampleData.sales,
    //   purchase: SampleData.purchase,
    // });
  }

  getPaymentCertStatus = (revoked,signed) => {
    if(revoked){
      return 'Revoked'
    } else {
        if(signed) {
           return 'Accepted'
        } else {
           return 'Pending'
        }
    }  
  }

  getPaymentData = (docHash = null) => {
    const response = getLocalDocumentsList()
    if(response) {
      let salesDoc = filterOnDocType(response.salesDoc,'paymentCertificate') ? filterOnDocType(response.salesDoc,'paymentCertificate') : []
      if(salesDoc){
        salesDoc = salesDoc.sort(function(a, b){ 
          return new Date(b.createdAt) - new Date(a.createdAt); 
        }).map((item)=>{
          if(docHash && item.docHash === docHash){
            return {
              ...item,
              status: this.getPaymentCertStatus(item.revoked,item.signed),
              finalAmt: numberWithCommas(formatAmount(item.finalAmt)),
              revoked: true,
              docType: 'Payment Certificate',
              createdAt: unixTimestampToDate(item.createdAt) + ' ' + getTimeFromUnixTimestamp(item.createdAt),
              updatedAt: unixTimestampToDate(item.updatedAt) + ' ' + getTimeFromUnixTimestamp(item.updatedAt)
            }
          } else {
            return {
              ...item,
              status: this.getPaymentCertStatus(item.revoked,item.signed),
              finalAmt: numberWithCommas(formatAmount(item.finalAmt)),
              docType: 'Payment Certificate',
              createdAt: unixTimestampToDate(item.createdAt) + ' ' + getTimeFromUnixTimestamp(item.createdAt),
              updatedAt: unixTimestampToDate(item.updatedAt) + ' ' + getTimeFromUnixTimestamp(item.updatedAt)
            }
          }
        })
      }
      let purchaseDoc = filterOnDocType(response.purchaseDoc, 'paymentCertificate') ? filterOnDocType(response.purchaseDoc, 'paymentCertificate') : []
      if(purchaseDoc){
        purchaseDoc = purchaseDoc.sort(function(a, b){ 
          return new Date(b.createdAt) - new Date(a.createdAt); 
        }).map((item)=> {
          if(docHash && item.docHash === docHash){
            return {
              ...item,
              status: this.getPaymentCertStatus(item.revoked,item.signed),
              finalAmt: numberWithCommas(formatAmount(item.finalAmt)),
              revoked: true,
              docType: 'Payment Certificate',
              createdAt: unixTimestampToDate(item.createdAt) + ' ' + getTimeFromUnixTimestamp(item.createdAt),
              updatedAt: unixTimestampToDate(item.updatedAt) + ' ' + getTimeFromUnixTimestamp(item.updatedAt)
            }
          } else {
            return {
              ...item,
              status: this.getPaymentCertStatus(item.revoked,item.signed),
              finalAmt: numberWithCommas(formatAmount(item.finalAmt)),
              docType: 'Payment Certificate',
              createdAt: unixTimestampToDate(item.createdAt) + ' ' + getTimeFromUnixTimestamp(item.createdAt),
              updatedAt: unixTimestampToDate(item.updatedAt) + ' ' + getTimeFromUnixTimestamp(item.updatedAt)
            }
          }
        })
      }
      this.setState({loader: false, issuedTo: salesDoc ,issuedBy: purchaseDoc})
    }
  }

  openModalQuotation = (data) => {
    this.setState({ loader1: true });
    signDocService
      .getDocHash(data.original.docHash)
      .then((response) => {
        if (response && response.data) {
          console.log('response.data : ', response.data);
          const { recipientDetails, issuerDetails, doc } = response.data;
          const docInfo = JSON.parse(response.data.doc.docInfo);
          const docType = response.data.doc.docType;
          doc['docInfo'] = docInfo;
          doc['docType'] = 'Payment Certificate';
          this.setState({
            loader1: false,
            openModalQuotation: true,
            document: {
              doc,
              issuerDetails,
              recipientDetails,
            },
          });
        }
      })
      .catch((error) => {
        console.log('Error : ', error);
        this.setState({ loader1: false });
      });
  };

  handleCloseQuotation = () => {
    this.setState({ openModalQuotation: false });
  };

  handleRevokedClick = async (data) => {
    // console.log('Data : ',data);
    this.setState({loader: true})
    let response = null
    try {
      response = await revokeDocument(data.docHash)
    } catch (error) {
     this.setState({loader: false})
      console.log('Error : ',error);
    }
    if(response){
     getDocumentListFun()
     this.getPaymentData(data.docHash)
    }
 }

  handleTabs = (event, newValue) => {
    this.setState({
      value: newValue,
    });
  };

  onRefresh = async (evt) => {
    this.setState({loader: true})
    try {
      await getDocumentListFun()
      this.getPaymentData()
    } catch (error) {
      this.setState({loader: false})
      console.log('Error : ',error);
    }
  }

  render() {
    if(this.state.loader){
      return <BackdropLoader open={this.state.loader} />
    }

    return (
      <div className='PaymentCertificateTab'>
        <div className="paymentHeader row">
          <h3>Payment Certificate</h3>
          <Tooltip title='refresh' placement="right-end">
            <IconButton color="primary" aria-label="upload picture" component="span" onClick={this.onRefresh}>
              <RefreshIcon />
            </IconButton>
          </Tooltip>
        </div>
        <AppBar position='static'>
          <Tabs
            variant='scrollable'
            indicatorColor='primary'
            className='tab-appbar'
            style={{ color: 'black' }}
            value={this.state.value}
            onChange={this.handleTabs}
            aria-label='simple tabs example'
          >
            <Tab className='tab' label='Sales' {...a11yProps(0)} />
            <Tab className='tab' label='Purchases' {...a11yProps(1)} />
          </Tabs>
        </AppBar>
        <TabPanel value={this.state.value} index={0}>
          <PaymentCertificateSales data={this.state.issuedTo}  onOpenModalQuotation={this.openModalQuotation} loader={this.state.loader1}/>
        </TabPanel>
        <TabPanel value={this.state.value} index={1} className='purchaseTab'>
          <PaymentCertificatePurchase data={this.state.issuedBy} onRevoked={this.handleRevokedClick}  onOpenModalQuotation={this.openModalQuotation} loader={this.state.loader1}/>
        </TabPanel>
        <Dialog
          open={this.state.openModalQuotation}
          onClose={this.handleCloseQuotation}
          aria-labelledby='simple-modal-title'
          aria-describedby='scroll-dialog-description'
          className='saleModaleTemplate'
        >
          <DialogTitle
            id='customized-dialog-title'
            onClose={this.handleCloseQuotation}
          >
            {this.state.document.doc ? this.state.document.doc.docType : ''}
          </DialogTitle>
          <DialogContent>
            {/* <DialogContentText id="scroll-dialog-description"> */}
               <PaymentCertTemplate document={this.state.document} showTerms={true} showDigitalSign={true}/>
            {/* </DialogContentText>   */}
          </DialogContent>
        </Dialog>
      </div>
    );
  }
}

export default PaymentCertificateTab;
