import React from 'react'
import { formatDate } from '../Shared/dateTimeFormat';
import * as XLSX from "xlsx";
import DICTUrl from "../../assets/saleTemplate.jpg";
import PDDMCSUrl from "../../assets/deliveryOrderTemplate.jpg";
import ADSMUrl from "../../assets/paymentCertificate.jpg";
import ADCSUrl from "../../assets/TVCertificateTemplate.jpg";
import PGDSLIUrl from "../../assets/invoice.jpg";

export default class TVCertificateQuotation extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            user: this.props.user,
            items: [],
            imageUrl: null,
        };
    }

    componentDidMount() {
        this.loadImage();
    }
    
    loadImage = () => {
        switch(this.props.template){
            case "DICT_Doc" :
                this.setState({imageUrl: DICTUrl});
                break;
            case "ADSM_Doc" :
                this.setState({imageUrl: ADSMUrl});
                break;   
            case "ADCS_Doc" :
                this.setState({imageUrl: ADCSUrl});
                break;      
            case "PGDSLI_Doc" :
                this.setState({imageUrl: PGDSLIUrl});
                break;
            case "PDDMCS_Doc" :
                this.setState({imageUrl: PDDMCSUrl});
                break;           
            default :  
                break;
        }
    }

    readExcel = (file) => {
        const promise = new Promise((resolve, reject) => {
            const fileReader = new FileReader();
            fileReader.readAsArrayBuffer(file);

            fileReader.onload = (e) => {
                const bufferArray = e.target.result;

                const wb = XLSX.read(bufferArray, { type: "buffer" });

                const wsname = wb.SheetNames[0];

                const ws = wb.Sheets[wsname];

                const data = XLSX.utils.sheet_to_json(ws);

                resolve(data);
            };

            fileReader.onerror = (error) => {
                reject(error);
            };
        });

        promise.then((d) => {
            this.setState({
                items: d
            })
        });
    };

    render() {
        
        return (
            <div>
                File uploaded: <a onClick={this.readExcel(this.props.fileObj)}>{this.props.fileObj.name}</a>
                <div>{this.props.formData.length} students uploaded</div>
                <div style={{paddingTop: "10px"}}>
                    <span style={{verticalAlign: "top", paddingRight: "10px"}}>Certificate Type:</span>
                    <img src={this.state.imageUrl} width="250px" height="325px" />
                </div>

                {/* <table class="table container">
                        <thead>
                            <tr>
                                <th scope="col">Item</th>
                                <th scope="col">Description</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.items.map((d) => (
                                <tr key={d.StudentName}>
                                    <td>{d.StudentName}</td>
                                    <td>{d.Description}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table> */}
            </div>
        )
    }
}