import React, { Component } from 'react';
import {
  Button,
  TextField,
  InputAdornment,
  IconButton,
} from '@material-ui/core';
import PersonOutlineIcon from '@material-ui/icons/PersonOutline';
import './login.css';
import { VisibilityOutlined, VisibilityOffOutlined } from '@material-ui/icons';
import LoginImage from './LoginImage';
import { Link } from 'react-router-dom';
import { BackdropLoader } from '../services/loader';
import authService from '../services/authService';
import { messagePopup } from '../services/messagePopupService';
import Typography from '@material-ui/core/Typography';

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      showPassword: false,
      show: false,
      loader: false,
      errors: { email: '', password: '' },
      isFinancier: false
    };
  }

  async componentDidMount(){
     if(this.props && this.props.location && this.props.location.state){
      const urlLink = this.props.location.state.urlLink;
      if(urlLink === 'verifyEmail'){   
        const emailEncode = this.props.location.state.emailEncode;
        try {
          this.setState({loader: true})
          const response = await authService.verifyEmail(emailEncode)
          console.log('Response : ',response);
          messagePopup('', 'Account activated successfully', 'success');
          this.setState({loader: false})
        } catch (error) {
          this.setState({loader: false})
          console.log('Error : ',error);
        }
      }
     }
     if(this.props && this.props.match && this.props.match.path !== '/login'){
       this.setState({isFinancier:true})
     }
  }

  handleChange = (e) => {
    if (e.target.id === 'email') {
      this.setState({ email: e.target.value });
    }

    if (e.target.id === 'password') {
      this.setState({ password: e.target.value });
    }
  };

  handleClickShowPassword = () => {
    this.setState({ showPassword: !this.state.showPassword });
  };

  handleMouseDownPassword = (e) => {
    e.preventDefault();
  };

  onJediD = () => {
    this.setState({ show: true });
  };

  onLogin = () => {
    this.setState({ show: false });
  };

  validateForm = (email, password) => {
    const { errors } = this.state;
    const validEmailRegex = RegExp(
      /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    );
    errors.email = validEmailRegex.test(email.trim())
      ? ''
      : 'Email is not valid!';
    errors.password =
      password.trim().length > 5
        ? ''
        : 'Password should be greater than 5 characters';
    this.setState({ errors });
    return this.state.errors.email === 'Email is not valid!' ||
      this.state.errors.password ===
        'Password should be greater than 5 characters'
      ? false
      : true;
  };

  handleLoginButtonClick = () => {
    const url = this.state.isFinancier ? '/login': '/loginFinancier'
    this.setState({isFinancier:!this.state.isFinancier})
    this.props.history.push(url)

  }

  onSubmit = async (e) => {
    e.preventDefault();
    if (this.validateForm(this.state.email, this.state.password)) {
      this.setState({ loader: true });
      try {
        const response = await authService.login(this.state);
        this.setState({ loader: false });
        if(this.state.isFinancier){
          if(response && response.data && response.data.role && response.data.role === "financier"){ 
            const url = this.props.location.state ? this.props.location.state.from : 'Certificate'
            this.props.history.push(url);
          } else {
            authService.logout()
            messagePopup('', 'Please enter valid username or password', 'error');
          }  
        } else {
          if(response && response.data && (!response.data.role || response.data.role === "enterprise")){ 
            const url = this.props.location.state ? this.props.location.state.from : 'Certificate'
            this.props.history.push(url);
          } else {
            authService.logout()
            messagePopup('', 'Please enter valid username or password', 'error');
          }  
        } 
      } catch (ex) {
        this.setState({ loader: false });
        if (ex.response && ex.response.status === 401) {
          messagePopup('', 'Please enter valid username or password', 'error');
        }
      }
    }
  };

  render() {
    return (
      <div className='signMain'>
        <BackdropLoader open={this.state.loader} />
        <LoginImage {...this.props}/>
        {this.state.show === true ? (
          <div className='signForm'>
            <div className="logoPart">
            <span className="logo" onClick={() => {this.props.history.push('')}}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            xlink="http://www.w3.org/1999/xlink"
            width="78"
            height="87"
            viewBox="0 0 78 87"
          >
            <defs>
              <clipPath id="clip-path">
                <rect
                  id="Rectangle_371"
                  data-name="Rectangle 371"
                  width="78"
                  height="87"
                  transform="translate(-2269 -5850)"
                  fill="#fff"
                  opacity="0.2"
                />
              </clipPath>
            </defs>
            <g
              id="logo"
              transform="translate(2269 5850)"
              clipPath="url(#clip-path)"
            >
              <g
                id="Group_1069"
                data-name="Group 1069"
                transform="translate(-3989.496 -5953.821)"
              >
                <path
                  id="Oval-3"
                  d="M37.529,6.955a38.144,38.144,0,0,0,6.084,75.8"
                  transform="translate(1701.713 146.92) rotate(-45)"
                  fill="none"
                  stroke="#fff"
                  strokeLinecap="square"
                  strokeWidth="9"
                  fillRule="evenodd"
                />
                <path
                  id="Oval-3-2"
                  data-name="Oval-3"
                  d="M81.755,44.614A38.187,38.187,0,0,0,70.912,17.976"
                  transform="translate(1702.054 146.92) rotate(-45)"
                  fill="none"
                  stroke="#fff"
                  strokeLinecap="square"
                  strokeWidth="9"
                  fillRule="evenodd"
                />
                <path
                  id="Path_435"
                  data-name="Path 435"
                  d="M.1,1.979l9.979-3.887-.031,31.94H31.026l-.031,9.953H10V44s-.037,5.375,7.8,7.348a15.406,15.406,0,0,0,11.313-1.856l6.127,6.252a26.852,26.852,0,0,1-22.372,3.5C-.248,53.378,0,44,0,44Z"
                  transform="translate(1744 108)"
                  fill="#fff"
                />
              </g>
            </g>
          </svg>
        </span>
        <span className="logoText">JupytonCert</span>
              </div>
            <h2>Login</h2>
            <h5>LOGIN WITH JED TRADE ID</h5>
            {/* <p id="description">
              <span>
                Lorem Ipsum dolor sit amet, consetetur sadIpscing elitr,
              </span>
              <span>sed diam nonumy eirmod tempor invidunt.</span>
            </p> */}

            <form noValidate onSubmit={this.onSubmit}>
              <TextField
                value={this.state.email}
                margin='normal'
                id='jedtradeid'
                name='jedtradeid'
                placeholder='Jed Trade Id'
                onChange={this.handleChange}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position='end' className='icon'>
                      <PersonOutlineIcon />
                    </InputAdornment>
                  ),
                }}
              />

              <TextField
                className='loginPassword'
                //   variant="outlined"
                margin='normal'
                id='password'
                name='password'
                placeholder='Password'
                type={this.state.showPassword ? 'text' : 'password'}
                value={this.state.password}
                onChange={this.handleChange}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position='end'>
                      <IconButton
                        aria-label='toggle password visibility'
                        onClick={this.handleClickShowPassword}
                        onMouseDown={this.handleMouseDownPassword}
                      >
                        {this.state.showPassword ? (
                          <VisibilityOutlined />
                        ) : (
                          <VisibilityOffOutlined />
                        )}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
              />
              <span className='forgotpassLink'>
                <Link to='/forgotPassword' variant='body2'>
                  Forgot Password?
                </Link>
              </span>
              <Button
                variant='contained'
                color='primary'
                type='submit'
                className='signBtn'
              >
                LOGIN
              </Button>
              <span>OR</span>
              <Button
                variant='contained'
                className='loginOptional'
                onClick={() => {
                  this.onLogin();
                }}
              >
                LOGIN WITH EMAIL ADDRESS
              </Button>
            </form>
            {/* {!this.state.isFinancier && (
              <Typography style={{marginTop:'30px',fontSize:'15px'}}>
                Don't have an account?{' '}
                <Link to='/createAccount' style={{textDecoration:'none'}}>
                  Create Account
                </Link>
              </Typography>
            )} */}
          </div>
        ) : (
          <div className='signForm'>
            <div className="logoPart" onClick={() => {this.props.history.push('')}}>
            <span className="logo">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            xlink="http://www.w3.org/1999/xlink"
            width="78"
            height="87"
            viewBox="0 0 78 87"
          >
            <defs>
              <clipPath id="clip-path">
                <rect
                  id="Rectangle_371"
                  data-name="Rectangle 371"
                  width="78"
                  height="87"
                  transform="translate(-2269 -5850)"
                  fill="#fff"
                  opacity="0.2"
                />
              </clipPath>
            </defs>
            <g
              id="logo"
              transform="translate(2269 5850)"
              clipPath="url(#clip-path)"
            >
              <g
                id="Group_1069"
                data-name="Group 1069"
                transform="translate(-3989.496 -5953.821)"
              >
                <path
                  id="Oval-3"
                  d="M37.529,6.955a38.144,38.144,0,0,0,6.084,75.8"
                  transform="translate(1701.713 146.92) rotate(-45)"
                  fill="none"
                  stroke="#fff"
                  strokeLinecap="square"
                  strokeWidth="9"
                  fillRule="evenodd"
                />
                <path
                  id="Oval-3-2"
                  data-name="Oval-3"
                  d="M81.755,44.614A38.187,38.187,0,0,0,70.912,17.976"
                  transform="translate(1702.054 146.92) rotate(-45)"
                  fill="none"
                  stroke="#fff"
                  strokeLinecap="square"
                  strokeWidth="9"
                  fillRule="evenodd"
                />
                <path
                  id="Path_435"
                  data-name="Path 435"
                  d="M.1,1.979l9.979-3.887-.031,31.94H31.026l-.031,9.953H10V44s-.037,5.375,7.8,7.348a15.406,15.406,0,0,0,11.313-1.856l6.127,6.252a26.852,26.852,0,0,1-22.372,3.5C-.248,53.378,0,44,0,44Z"
                  transform="translate(1744 108)"
                  fill="#fff"
                />
              </g>
            </g>
          </svg>
        </span>
        <span className="logoText">JupytonCert</span>
              </div>
            <h2>Login</h2>
            <h5>{this.state.isFinancier ? 'Financial Institute' : 'Welcome to JupytonCert'}</h5>
            {/* {!this.state.isFinancier && (
              <p id="description">
                <span>
                  Lorem Ipsum dolor sit amet, consetetur sadIpscing elitr,
                </span>
                <span>sed diam nonumy eirmod tempor invidunt.</span>
              </p>
            )} */}

            <form noValidate onSubmit={this.onSubmit}>
              <TextField
                value={this.state.email}
                margin='normal'
                id='email'
                name='email'
                placeholder='Email Address'
                onChange={this.handleChange}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position='end'>@</InputAdornment>
                  ),
                }}
                helperText={
                  this.state.errors.email ? this.state.errors.email : ''
                }
              />
              <TextField
                className='loginPassword'
                margin='normal'
                id='password'
                name='password'
                placeholder='Password'
                type={this.state.showPassword ? 'text' : 'password'}
                value={this.state.password}
                onChange={this.handleChange}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position='end'>
                      <IconButton
                        aria-label='toggle password visibility'
                        onClick={this.handleClickShowPassword}
                        onMouseDown={this.handleMouseDownPassword}
                      >
                        {this.state.showPassword ? (
                          <VisibilityOutlined />
                        ) : (
                          <VisibilityOffOutlined />
                        )}
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
                helperText={
                  this.state.errors.password ? this.state.errors.password : ''
                }
              />
              <span className='forgotpassLink'>
                <Link to='/forgotPassword' variant='body2'>
                  Forgot Password?
                </Link>
              </span>
              <Button
                variant='contained'
                color='primary'
                type='submit'
                className='signBtn'
              >
                LOGIN
              </Button>
              <span>OR</span>
              <Button
                variant='contained'
                className='loginOptional'
                onClick={() => {
                  this.onJediD();
                }}
              >
                LOGIN WITH JED ID
              </Button>
            </form>
            {/* {!this.state.isFinancier && (
              <>
                <Typography style={{marginTop:'30px',fontSize:'15px'}}>
                   Don't have an account?{' '}
                   <Link to='/createAccount' style={{textDecoration:'none'}}>
                      Create Account
                   </Link>
                </Typography>
              </>
            )} */}
            {/* <Typography style={{fontSize:'15px', marginTop:'18px' }}>
                <Button onClick={this.handleLoginButtonClick} color="primary" style={{textTransform:'none'}}>
                   {!this.state.isFinancier ? 'Login as Financier' : 'Login as Enterprise'}
                </Button>
            </Typography> */}
      
          </div>
        )}
      </div>
    );
  }
}

export default Login;
